import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Divider, Drawer, List, ListItem, ListItemIcon, ListItemText, Toolbar, makeStyles } from '@material-ui/core';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.default,
    padding: theme.spacing(3),
  },
}));

function FileManager() {
  const classes = useStyles();
  const [files, setFiles] = useState([]);
  const [selectedFile, setSelectedFile] = useState(null);
  useEffect(() => {
    axios.get('http://192.168.29.92:8001/files', {
      headers: {
        'Authorization': 'Bearer eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhdWQiOiJhdWRpZW5jZSIsImlzcyI6ImVudmlyb25tZW50LmNvbmZpZy5wcm9wZXJ0eSgpLmdldFN0cmluZygpIiwiZXhwIjoxNzE0NzU5NzUwLCJ1c2VySWQiOiJzdWJoYW0xIn0.V5mQ21GXhyp6YzKPmG1FrC1bpjhOJ491eNgChGz2q_I',
        'Content-Type': 'application/json'
      }
    })
      .then(response => response.data)
      .then(data => setFiles(data))
      .catch(error => console.log(error));
  }, []);

  function handleFileClick(file) {
    setSelectedFile(file);
  }

  function handleDeleteClick() {
    if (selectedFile) {
      // Delete the selected file from the server
      fetch(`/api/files/${selectedFile}`)
        .then(() => {
          // Update the list of files
          setFiles(files.filter(file => file !== selectedFile));
          setSelectedFile(null);
        })
        .catch(error => console.error(error));
    }
  }


  return (
    <div className="file-manager">
      <Drawer
        className={classes.drawer}
        variant="permanent"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"
      >
        <div className={classes.toolbar} />
        <Divider />
        <List>
          {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <List>
          {['All mail', 'Trash', 'Spam'].map((text, index) => (
            <ListItem button key={text}>
              <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
              <ListItemText primary={text} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </div>
  );
}

export default FileManager;